// style layout
@mixin classOutsetLeftRightDetail {

    &.outset-left {
        margin-left: auto;
    }

    &.outset-right {
        margin-right: auto;
    }
}

@mixin maxWidthFullWiwthDetail {

    &.full-width,
    &.max-width {
        width: 100%;
    }

}

// style table in side blockquote, notebox
@mixin table {
    table {

        &.full-width,
        &.max-width {
            width: 100%;
        }
    }

    @include tableNotBorder();

}

@mixin space {
    >* {
        margin-top: 16px;

        &:first-child {
            margin-top: 0;
        }
    }
}

@mixin tableNotBorder {

    td,
    th {
        padding: 0 !important;
        border: none !important;
        display: block;
        align-items: center;
    }
}

@mixin classLeftRight {

    &.align-left {
        width: 50%;
        float: left;
        margin-right: 16px;
        margin-bottom: 16px;
    }

    &.align-right {
        width: 50%;
        float: right;
        margin-left: 16px;
        margin-bottom: 16px;
    }

}

@mixin maxWidth {
    &.max-width {
        position: relative;
        left: 50%;
        transform: translateX(-50%);
        width: calc(100vw - 17px);
        max-width: 1300px;
    }
}

@mixin alignLeftRight {
    &[align="center"] {
        width: 100%;
        @include tableNotBorder();
        text-align: left;
    }

    &[align="left"] {
        width: 50%;
        float: left;
        margin-right: 16px;
        margin-bottom: 16px;
        @include tableNotBorder();
        text-align: left;
    }

    &[align="right"] {
        width: 50%;
        float: right;
        margin-left: 16px;
        margin-bottom: 16px;
        @include tableNotBorder();
        text-align: left;
    }
}

.zce-content-body {
    table {

        td,
        th {
            padding: 0;
        }
    }

    img,
    video {
        max-width: 100%;
        width: auto;
        display: flex;
        margin: 0 auto;
    }

    figcaption,
    .caption {
        font-size: 16px;
        line-height: 21px;
        color: #6C757D;
        text-align: center;
        margin-top: 8px;
        display: block;
        font-weight: $font-weight-normal;

        >* {
            margin-top: 0;
        }
    }

    table:not([class]) {
        width: 100%;

        td,
        th {
            padding: 10px;
            border: 1px solid #E9ECEF;
        }
    }

    figure,
    table,
    blockquote,
    .notebox,
    .poll-box {
        @include classLeftRight();
        @include alignLeftRight();
        @include maxWidth();
    }

    blockquote,
    .notebox {
        @include space();
        @include table();
    }

    .live-zone,
    .interview-zone {

        table,
        figure {
            @include maxWidthFullWiwthDetail();
            @include classOutsetLeftRightDetail();
        }
    }
}

