// for detail-page
.site-body {
    >.container:not(.longform) {
        .zce-content-body {

            figure,
            table,
            blockquote,
            .notebox,
            .poll-box {
                @include classOutsetLeftRightDetail();
            }

            table {
                @include maxWidthFullWiwthDetail();
            }

            blockquote {
                &.outset-left {
                    padding-left: 0;
                    padding-right: 60px;
                }

                &.outset-right {
                    padding-right: 0;
                    padding-left: 60px;
                }
            }
        }
    }
}